
#app {
  font-family: "Roboto Regular", sans-serif;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
}
.header {
  background: #4a90e2;
}
.container {
  max-width: 600px;
  margin: 0 auto;
}
.filter__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.filter {
  cursor: pointer;
  text-align: center;
  padding: 15px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #00408b;
}
.filter-active {
    color: white;
    border-bottom: 5px solid #00408b;
}
.filter:hover {
    background-color: #3483de;
}
.body {
  background: #e1e1e1;
  min-height: 100vh;
}
.newTodo {
  position: relative;
}
.newTodo__input {
  width: 100%;
  border-radius: 5px;
  height: 45px;
  outline: none;
  border: 1px solid #eee;
  padding-left: 20px;
  font-size: 16px;
  margin: 20px auto;
  -webkit-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
}
.newTodo__input:focus {
    border: 1px solid #00408b;
}
::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ddd;
  opacity: 1;
  /* Firefox */
}
:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ddd;
  opacity: 1;
  /* Firefox */
}
::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ddd;
  opacity: 1;
  /* Firefox */
}
::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ddd;
  opacity: 1;
  /* Firefox */
}
.leftWording {
  font-family: "Roboto Italic";
  font-style: italic;
  font-size: 24px;
  color: #c8c8c8;
  margin: 0;
  padding-bottom: 10px;
}

.todoItem {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 20px 10px 0;
  background: #f2f2f2;
  margin-bottom: 5px;
}
.todoItem-isCritical {
    background: #fff2dc;
}
.todoItem__panelContainer {
  -ms-flex-preferred-size: 20px;
      flex-basis: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: move;
}
.todoItem__panel {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 50%;
}
.todoItem:hover .todoItem__dot {
  display: block;
  -ms-flex-preferred-size: 4px;
      flex-basis: 4px;
  width: 4px;
  background-color: #d8d8d8;
  border-radius: 50%;
}
.todoItem__input {
  cursor: pointer;
  width: 35px;
  padding: 5px;
  font-size: 18px;
}
.todoItem__check {
  display: none;
}
.todoItem__boxFa {
  color: #fff;
  background-color: #fff;
}
.todoItem__checkFa {
  color: #4a90e2;
}
.todoItem__content {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 400px;
          flex: 1 0 400px;
  margin: 0;
}
.todoItem__title {
  font-family: "Roboto Medium";
  font-size: 24px;
  margin: 0;
}
.todoItem__title-isDone {
    text-decoration: line-through;
    color: #9b9b9b;
}
.todoItem__status {
  color: #9b9b9b;
}
.todlItem__statusIcon {
  margin-right: 10px;
}
.todoItem__tool {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.todoItem__starFa {
  cursor: pointer;
  margin-right: 15px;
}
.todoItem__starFa-isCritical {
    color: #f5a623;
}
